body{
  margin: 0px;
}
header{
  display: flex;
  align-items: center;
}
.youtube{
  background-color: white;
  width: 300px;
  margin-top: -100px;
}
.search{
  align-items: center;
  height: 35px;
  width: 500px;
  margin-top: -100px;
  margin-left: 250px;
  border-radius: 20px;
  border: 1px solid rgb(87, 87, 87);
  padding: 10px 40px 10px 20px;
}
i{
  margin-top: -100px;
  margin-left: -30px;
  color:rgb(87, 87, 87) ;
}
.fa-microphone{
  margin-top: -100px;
  margin-left: 30px;
  color:black ;
  font-size: 20px;
  background-color: rgb(218, 218, 218);
  border: 1px solid white;
  padding: 7px 5px 5px 10px;
  border-radius: 50%;
  height: 20px;
  width: 20px;
  cursor: pointer;
}
.card-container{
  width: 80%;
  margin: -70px auto;
  display: flex;
  flex-wrap: wrap;
}
.card{
  width: 31.5%;
  border-radius: 15px;
  margin-bottom: 30px;
  margin-left: 20px;
  cursor: pointer;
}
.thumbnail{
  border-radius: 15px;
  height: 200px;
  width: 100%;
}
.logo-title{
  display: flex;
}
.logo{
  border-radius: 50%;
  width: 40px;
  height: 40px;
  padding: 0px 10px;
}
h5{
  margin: 0px;
  padding-left: 16%;
  color: gray;
  margin-top: 10px;
  font-weight: 500;
  font-size: 15px;
}
.views-likes{
  display: flex;
  margin-top: -10px;
  margin-left: 13%;
}
.views-likes p{
  margin: 10px;
  color: rgb(68, 68, 68);
}
.title{
  font-size: 15px;
  font-weight: 600;
  padding: 0px 3px 0px 0px;
  margin: 0px;
}
.fa-circle-check{
  margin-left: 0px;
  font-size: 10px;
}